@font-face {
  font-family: Inter;
  src: local(Inter), url("Inter-Regular.483683b3.woff2") format("woff2"), url("Inter-Regular.01de2896.woff") format("woff");
  font-style: normal;
  font-weight: regular;
}

@font-face {
  font-family: Mulish;
  src: local(Mulish), url("Inter-Regular.483683b3.woff2") format("woff2"), url("Inter-Regular.01de2896.woff") format("woff");
  font-style: normal;
  font-weight: regular;
}

html, body {
  height: 100%;
  letter-spacing: .36px;
  color: #7e7e7e;
  font-family: Inter, sans-serif;
  font-size: 17px;
  font-style: normal;
  font-weight: 400;
  line-height: 159%;
}

.header__container {
  position: relative;
}

.header__top {
  background-color: #f2f2f2;
  padding: 20px 10px;
}

.header__main {
  background-color: #fff;
  padding: 0 10px;
}

.header-top__container {
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin: 0 auto;
  display: flex;
}

.header-top__time {
  color: var(--color-blue);
}

.header-social__list {
  gap: 10px;
  display: flex;
}

.header-social__link svg:hover #facebook, .header-social__link svg:hover #twitter, .header-social__link svg:hover #p, .header-social__link svg:hover #instagram1, .header-social__link svg:hover #instagram2 {
  fill: var(--color-yellow);
}

.header-main__container {
  max-width: 1200px;
  justify-content: space-between;
  gap: 10px;
  margin: 0 auto;
  padding: 30px 0 60px;
  display: flex;
}

.header-contacts {
  gap: 65px;
  display: flex;
}

.header-contacts__container {
  align-items: center;
  gap: 5px;
  display: flex;
}

.header-contacts__column {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 2px;
  display: flex;
}

.header-contacts__img svg:hover #mail1, .header-contacts__img svg:hover #phone1 {
  fill: var(--color-blue);
}

.header-contacts__img svg:hover #mail2, .header-contacts__img svg:hover #mail3, .header-contacts__img svg:hover #phone2 {
  fill: var(--color-yellow);
}

.header-contacts__text {
  color: var(--color-blue);
  letter-spacing: .36px;
  font-family: Mulish;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.header-contacts__text:hover {
  color: var(--color-yellow);
}

.header-contacts__link {
  color: #7e7e7e;
}

.header-contacts__link:hover {
  color: var(--color-yellow);
}

.header-bottom__container {
  max-width: 1200px;
  background-color: var(--color-yellow);
  z-index: 20;
  align-items: center;
  gap: 10px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 190px;
  left: 10px;
  right: 10px;
}

.header-bottom__nav {
  flex: 1;
  font-size: 19px;
}

.header-bottom__list {
  justify-content: space-around;
  gap: 30px;
  display: flex;
}

.header-bottom__item a {
  color: #222;
  transition: all .3s ease-out;
}

.header-bottom__item a:hover {
  color: #fff;
  transition: all .3s ease-out;
}

.header-bottom__button {
  background-color: var(--color-blue);
  color: #f4f4f4;
}

.header-bottom__button:hover {
  border: 1px solid var(--color-blue);
  color: #1c2752;
  background-color: #f4f4f4;
}

@media (max-width: 1000px) {
  .header-main__container {
    flex-direction: column;
    padding: 10px 0;
  }

  .header-contacts {
    justify-content: space-between;
    gap: 15px;
  }

  .header-bottom__container {
    flex-direction: column;
    position: static;
  }

  .header-bottom__nav {
    width: 100%;
    font-size: 19px;
  }

  .header-bottom__button {
    width: 100%;
  }
}

@media (max-width: 650px) {
  .header-main__container {
    align-items: center;
  }

  .header-contacts {
    flex-direction: column;
  }

  .header-bottom__list {
    text-align: center;
    flex-direction: column;
    gap: 10px;
  }

  .header-bottom__item {
    padding: 10px 0;
  }
}

*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

nav, footer, header, aside {
  display: block;
}

input, button, textarea {
  font-family: inherit;
}

.input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a, a:visited, a:hover {
  text-decoration: none;
}

ul, ol, li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-style: inherit;
  font-weight: inherit;
}

.wrapper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

._ibg {
  position: relative;
}

._ibg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.footer__container {
  background-color: var(--color-blue);
  padding: 140px 120px;
}

.footer__top {
  border-bottom: 1px solid var(--color-light-grey);
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 0 0 40px;
  display: flex;
}

.top-footer__contacts {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
}

.top-footer__item {
  align-items: center;
  gap: 10px;
  display: flex;
}

.top-footer__img svg:hover #phone2, .top-footer__img svg:hover #mail2, .top-footer__img svg:hover #location1 {
  fill: var(--color-white);
}

.top-footer__column {
  flex-direction: column;
  display: flex;
}

.top-footer__text {
  color: var(--color-white);
}

.top-footer__text:hover {
  color: var(--color-yellow);
}

.top-footer__link {
  color: var(--color-white);
}

.top-footer__link:hover {
  color: var(--color-yellow);
}

.bottom-footer {
  border-bottom: 1px solid var(--color-white);
  gap: 20px;
  padding: 40px 0 0;
  display: flex;
}

.bottom-footer__social {
  flex: 33%;
}

.bottom-footer__navigation {
  flex: 64%;
  justify-content: space-around;
  padding: 0 0 50px;
  display: flex;
}

.bottom-footer__item :not(:last-child) {
  padding: 0 0 10px;
}

.bottom-footer__title {
  color: var(--color-yellow);
  padding: 0 0 20px;
  font-size: 20px;
}

.bottom-footer__link {
  color: var(--color-white);
  font-size: 17px;
  line-height: 159%;
}

.bottom-footer__link:hover {
  color: var(--color-yellow);
}

.social-footer {
  flex-direction: column;
  gap: 50px;
  display: flex;
}

.social-footer__text {
  color: var(--color-white);
}

.social-footer__links {
  justify-content: flex-start;
  gap: 10px;
  display: flex;
}

.social-footer__link svg:hover {
  fill: var(--color-yellow);
}

@media (max-width: 1300px) {
  .footer__top {
    flex-direction: column;
  }
}

@media (max-width: 1000px) {
  .footer__container {
    padding: 40px 10px;
  }

  .top-footer__contacts {
    justify-content: space-between;
  }

  .top-footer__item {
    flex-direction: column;
  }
}

.bottom-footer {
  flex-direction: column;
}

.social-footer {
  text-align: center;
  align-items: center;
}

@media (max-width: 650px) {
  .top-footer__contacts {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .top-footer__item {
    flex-direction: row;
  }

  .bottom-footer__navigation {
    flex-direction: column;
    flex: 100%;
    gap: 20px;
  }
}

._container {
  max-width: 1440px;
  margin: 0 auto;
}

.main-block {
  position: relative;
}

.main-block__background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.main-block__background img {
  object-position: right;
}

.main-block__body {
  padding: 270px 600px 230px 120px;
  position: relative;
}

.main-block__text {
  color: #fff;
  background-color: #fff3;
  padding: 5px 40px 5px 60px;
  line-height: 175%;
  position: relative;
}

.main-block__text:before {
  content: "";
  width: 30px;
  height: 30px;
  background: url("Vector.457d24f3.svg");
  display: block;
  position: absolute;
  left: 20px;
}

.main-block__title {
  letter-spacing: 1.2px;
  color: #fff;
  padding: 30px 0;
  font-family: Mulish;
  font-size: 60px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.main-block__button {
  color: #1c2752;
  background-color: var(--color-yellow);
}

.main-block__button:hover {
  background-color: #f4f4f4;
  border: 1px solid #0000;
}

@media (max-width: 1350px) {
  .main-block__body {
    padding: 200px 200px 200px 120px;
  }
}

@media (max-width: 1000px) {
  .main-block__body {
    padding: 100px 100px 100px 10px;
  }

  .main-block__title {
    font-size: 40px;
  }
}

@media (max-width: 650px) {
  .main-block__body {
    padding: 30px 10px;
  }

  .main-block__button {
    width: 100%;
  }
}

.page {
  flex: auto;
}

:root {
  --color-yellow: #ffb342;
  --color-blue: #1c2752;
  --color-white: #fff;
  --color-grey: #7e7e7e;
  --color-light-grey: #f4f4f4;
}

._button {
  letter-spacing: .36px;
  border: 1px solid #0000;
  justify-content: center;
  align-items: center;
  padding: 20px 45px;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  transition: all .3s ease-out;
  display: inline-flex;
}

._button:hover {
  transition: all .3s ease-out;
}

._title {
  letter-spacing: .36px;
  color: var(--color-blue);
  padding: 20px 0;
  font-family: Mulish;
  font-size: 45px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

@media (max-width: 650px) {
  ._title {
    font-size: 30px;
  }
}

.welcome__container {
  background-color: var(--color-white);
}

.welcome__body {
  gap: 50px;
  padding: 140px 120px;
  display: flex;
}

.welcome__image {
  flex: 0 50%;
}

.welcome__info {
  flex-direction: column;
  flex: 0 50%;
  gap: 30px;
  display: flex;
}

.welcome__top span {
  color: var(--color-blue);
  background-color: #f4f4f4;
  padding: 5px 20px 5px 50px;
  position: relative;
}

.welcome__top span:before {
  content: "";
  height: 27px;
  width: 23px;
  background: url("Vector.be686a59.svg");
  display: block;
  position: absolute;
  left: 12px;
}

.welcome__text {
  padding: 5px 0;
}

.welcome__bottom {
  gap: 10px;
  display: flex;
}

.welcome__experience {
  text-align: center;
  background: #f4f4f4;
  flex-direction: column;
  flex: 0 33.33%;
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
  display: flex;
}

.welcome__experience span {
  color: var(--color-yellow);
  padding: 0 0 15px;
  font-family: Mulish;
  font-size: 80px;
  font-style: normal;
  line-height: 110%;
}

.welcome__experience p {
  color: var(--color-blue);
  font-family: Mulish;
  font-size: 20px;
  line-height: 130%;
}

.welcome__advantages {
  flex: 0 66.66%;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (max-width: 1000px) {
  .welcome__container {
    padding: 0 10px;
  }

  .welcome__body {
    flex-direction: column-reverse;
    gap: 20px;
    padding: 40px 0;
  }

  .welcome__image {
    padding: 30%;
  }

  .welcome__title {
    text-align: center;
  }

  .welcome__experience, .welcome__advantages {
    flex: 0 50%;
  }
}

.professional__container {
  background-color: var(--color-yellow);
}

.professional__body {
  padding: 140px 120px 0;
}

.main-professional {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px 40px;
}

.main-professional__body {
  color: var(--color-blue);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.main-professional span {
  background-color: #fff;
  padding: 5px 20px 5px 50px;
  position: relative;
}

.main-professional span:before {
  content: "";
  width: 23px;
  height: 27px;
  background: url("Professional1.ba130c6b.svg");
  display: block;
  position: absolute;
  top: 5px;
  left: 20px;
}

.main-professional__text {
  text-align: center;
  padding: 10px 0 35px;
}

.main-professional__list {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.main-professional__link {
  border: 1px solid var(--color-blue);
  color: var(--color-blue);
  padding-left: 70px;
}

.main-professional__link:hover {
  background-color: #fff;
  border: 1px solid #0000;
}

.main-professional__link_active {
  border: 1px solid var(--color-blue);
  background-color: #fff;
}

#professional-button-one {
  position: relative;
}

#professional-button-one:before {
  content: "";
  width: 36px;
  height: 36px;
  background: url("Professional2.f4cc6d49.svg");
  display: block;
  position: absolute;
  top: 15px;
  left: 30px;
}

#professional-button-two {
  position: relative;
}

#professional-button-two:before {
  content: "";
  width: 34px;
  height: 34px;
  background: url("Professional3.f0799a45.svg");
  display: block;
  position: absolute;
  top: 15px;
  left: 30px;
}

#professional-button-three {
  position: relative;
}

#professional-button-three:before {
  content: "";
  width: 34px;
  height: 34px;
  background: url("Professional4.f41d0d74.svg");
  display: block;
  position: absolute;
  top: 15px;
  left: 30px;
}

.slide-professional {
  max-width: 1200px;
  background-color: #fff;
  gap: 50px;
  margin-bottom: -60px;
  padding: 60px;
  display: flex;
  position: relative;
  box-shadow: -1px 6px 18px #00000017;
}

.slide-professional__body {
  flex: 0 50%;
}

.slide-professional__body span {
  background-color: #f4f4f4;
  padding: 5px 20px 5px 50px;
  position: relative;
}

.slide-professional__body span:before {
  content: "";
  height: 28px;
  width: 28px;
  background: url("Professional2.f4cc6d49.svg");
  display: block;
  position: absolute;
  left: 10px;
}

.slide-professional__description {
  flex-direction: column;
  gap: 10px;
