}

.slide-professional__image {
  flex: 0 50%;
}

.slide-professional__image img {
  object-position: right;
}

@media (max-width: 1000px) {
  .professional__body {
    padding: 40px 0 0;
  }

  .main-professional__body span {
    align-self: flex-start;
  }

  .main-professional__text {
    text-align: start;
  }

  .main-professional__list {
    flex-direction: column;
    gap: 10px;
  }

  .main-professional__link {
    width: 100%;
  }

  .slide-professional {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 0;
    padding: 10px;
    position: static;
  }

  .slide-professional__image {
    padding: 30%;
  }
}

.services__container {
  background-color: #f4f4f4;
}

.services__body {
  flex-direction: column;
