  margin: 0 auto;
  padding: 200px 120px 140px;
  display: flex;
}

.services__top {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.services__top span {
  color: var(--color-blue);
  background-color: #fff;
  padding: 5px 20px 5px 50px;
  position: relative;
}

.services__top span:before {
  content: "";
  width: 22px;
  height: 27px;
  background: url("ServicesSpan.7fc1d8af.svg");
  display: block;
  position: absolute;
