.whychooseus__container {
  display: flex;
}

.whychooseus__left {
  flex: 0 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.whychooseus__right {
  flex: 0 50%;
}

.left-whychooseus {
  background-image: url("WhycgooseusImage.d4020930.jpg");
  background-position: top;
  background-size: cover;
}

.left-whychooseus__body {
  background: #1c2752db;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding: 60px;
  display: flex;
}

.left-whychooseus__item {
  display: flex;
}

.left-whychooseus__number {
  width: 100px;
  height: 67px;
  background: var(--color-yellow);
  color: var(--color-blue);
  border: 1px solid #ffb342;
  border-radius: 0;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  display: flex;
}

.left-whychooseus__button {
  width: 290px;
  height: 67px;
  color: #fff;
  z-index: 1;
  border: 1px solid #fff;
  border-left: none;
  justify-content: flex-start;
  align-items: center;
  padding: 0 0 0 20px;
  font-size: 25px;
  display: flex;
  position: relative;
}

.left-whychooseus__button:hover {
  color: var(--color-blue);
  transition: color .3s ease-in;
}

.left-whychooseus__button:before {
  content: "";
  width: 0;
  height: 67px;
  z-index: -1;
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
}

.left-whychooseus__button:hover:before {
  animation: .3s ease-in both toleft;
}

.right-whychooseus__body {
  flex-direction: column;
  gap: 40px;
  padding: 100px 70px;
  display: flex;
}

.right-whychooseus__top span {
  background-color: var(--color-light-grey);
  color: var(--color-blue);
  padding: 5px 20px 5px 50px;
  position: relative;
}

.right-whychooseus__top span:before {
  content: "";
  width: 22px;
  height: 27px;
  background: url("ServicesSpan.7fc1d8af.svg");
  display: block;
  position: absolute;
  top: 0;
  left: 20px;
}

.right-whychooseus__items {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.right-whychooseus__item {
  padding: 0 0 0 45px;
  position: relative;
}

.right-whychooseus__item:before {
  content: "";
  width: 32px;
  height: 32px;
  background-image: url("WhychooseusIcon.db3e52b5.svg");
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.right-whychooseus__title {
  letter-spacing: .36px;
  color: var(--color-blue);
  padding: 0 0 10px;
  font-size: 20px;
  line-height: 140%;
}

@media (max-width: 1000px) {
  .whychooseus__container {
    flex-direction: column-reverse;
  }

  .whychooseus__left {
    display: block;
  }

  .left-whychooseus__body {
    gap: 20px;
    padding: 40px 10px;
  }

  .left-whychooseus__item, .left-whychooseus__button {
    width: 100%;
  }

  .left-whychooseus__button:hover {
    background-color: var(--color-yellow);
    border: 1px solid var(--color-yellow);
  }

  .left-whychooseus__button:hover:before {
    animation: none;
  }

  .right-whychooseus__body {
    gap: 20px;
    padding: 40px 10px;
  }

  .right-whychooseus__items {
    gap: 10px;
  }
}

@keyframes toleft {
  0% {
    width: 1px;
    background-color: var(--color-yellow);
  }

